@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Balsamiq Sans', cursive;
}

.container {
  margin: 0 auto 150px;
  min-width: 420px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.row {
  max-width: 680px;
  display: flex;
  flex-wrap: wrap;
}

.box {
  flex: 0 0 20%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
  border-radius: 2px;
  height: 100px;
  width: 80px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  position: relative;
  transition: all 300ms linear;
}

.moves {
  display: flex;
  align-items: center;
  flex: 1 1 100%;
}

.box:hover {
  cursor: pointer;
}

#empty-box {
  background-color: transparent;
  box-shadow: none;
  z-index: 99;
  cursor: not-allowed;
}

button {
  border: none;
	cursor: pointer;
	padding: 25px 80px;
	display: inline-block;
	margin: 15px 30px;
	letter-spacing: 1px;
	outline: none;
	position: relative;
  font-family: 'Balsamiq Sans', cursive;
  background: #cb4e4e;
	color: #fff;
	box-shadow: 0 6px #ab3c3c;
 transition: none;
  border-radius: 0 0 5px 5px;
}

button:hover {
	box-shadow: 0 8px #ab3c3c;
	top: -2px;
}

button:active {
	box-shadow: 0 0 #ab3c3c;
	top: 6px;
}

#reset {
  box-shadow: 0 6px #4593e3;
  background: #226fbe;
}

#reset:hover {
	box-shadow: 0 8px #4593e3;
	top: -2px;
}

#reset:active {
	box-shadow: 0 0 #4593e3;
	top: 6px;
}

.top {
  display: flex;
  margin-right: 40px;
}

h4 {
  font-size: 26px;
  margin: 0;
  padding:0;
}

@media screen and (max-width: 767px) {
  .box {
      height: 50px;
      width: 50px;
      font-size: 24px;
  }

  .container {
      margin-top: 10px;
  }
}

.newGameBtn {
  background-color: #6fb565;
  box-shadow: 0 6px #b3f0aa;
}

.newGameBtn:hover {
	box-shadow: 0 8px #b3f0aa;
	top: -2px;
}

.newGameBtn:active {
	box-shadow: 0 0 #b3f0aa;
	top: 6px;
}
.highScore h4 {
  margin: 6px 0px;
}

.highScore .score {
  font-size: 28px;
  background: #c3c3;
  border-radius: 50px;
  display: inline-block;
  width: 40px;
  padding: 5px;
  margin: 0px 6px;
}

.highScore .score:nth-of-type(odd) {
  background-color: #69d2ff;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

